import React, { useState } from 'react';
import PropTypes from 'prop-types';
import ReactRouterPropTypes from 'react-router-prop-types';
import { connect } from 'react-redux';
import PrimaryGap from '../BaseComponents/PrimaryGap';
import CustomizedButton from '../BaseComponents/CustomizedButton';
import Text from '../BaseComponents/Text';
import TextInput from './TextInput';
import BackHeadWithUsername from '../middleComponents/BackHeadWithUsername';
import LayOut from './LayOut';
import { setEmail as setEmailActionCreator } from '../actionCreators/currentUser';

function isEmail(value) {
  const reg = /^[a-zA-Z.]+@[a-zA-Z]+.[a-zA-Z]+$/;
  return reg.test(value);
}

function Email({ email, setEmail, history }) {
  const [newEmail, setNewEmail] = useState('');
  function handleChange(e) {
    setNewEmail(e.target.value);
  }
  function handleSave() {
    setEmail(newEmail);
    history.goBack();
  }
  return (
    <LayOut
      narrowHead={<BackHeadWithUsername title="" />}
      rightAside={(
        <>
          <PrimaryGap />
          <TextInput
            labelText=""
            readOnly
            value={email}
            onChange={null}
          />
          <TextInput
            labelText=""
            value={newEmail}
            onChange={handleChange}
            autoFocus
          />
          <PrimaryGap />
          <div style={{ padding: '9px', backgroundColor: 'white' }}>
            <Text secondary>
             Twitter 
            </Text>
          </div>
          <PrimaryGap />
          <div style={{
            display: 'flex', justifyContent: 'flex-end', padding: '9px', backgroundColor: 'white',
          }}
          >
            <CustomizedButton
              filled
              onClick={handleSave}
              disabled={
                newEmail === email
                || !isEmail(email)
              }
            >
              
            </CustomizedButton>
          </div>
        </>
      )}
    />
  );
}
Email.propTypes = {
  email: PropTypes.string.isRequired,
  setEmail: PropTypes.func.isRequired,
  history: ReactRouterPropTypes.history.isRequired,
};

const mapStateToProps = state => ({
  email: state.currentUser && state.currentUser.email,
});
const mapDispathToProps = dispatch => ({
  setEmail: email => dispatch(setEmailActionCreator(email)),
});
export default connect(
  mapStateToProps,
  mapDispathToProps,
)(Email);
