import React from 'react';
import Text from '../BaseComponents/Text';
import CustomizedButton from '../BaseComponents/CustomizedButton';
import PullDownRefresh from '../middleComponents/PullDownRefresh';
import Layout from '../layout/Layout';
import RightAside from './RightAside';
import HomePageNarrowHead from '../layout/HomePageNarrowHead';

function Message() {
  function handlRefresh() {
    return new Promise((resolve) => {
      setTimeout(() => resolve(), 1000);
    });
  }
  return (
    <Layout
      reverse
      narrowHead={(
        <HomePageNarrowHead
          middle={<Text large></Text>}
        />
      )}
      main={(
        <PullDownRefresh onRefresh={handlRefresh}>
          <div style={{ padding: '37px 18px', textAlign: 'center' }}>
            <div style={{ marginBottom: '9px' }}>
              <Text xlarge bold></Text>
            </div>
            <div>
              <Text secondary> Twitter </Text>
            </div>
            <div style={{ display: 'flex', justifyContent: 'center', marginTop: '18px' }}>
              <CustomizedButton filled></CustomizedButton>
            </div>
          </div>
        </PullDownRefresh>
      )}
      rightAside={(
        <RightAside />
      )}
    />
  );
}
export default Message;
