import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import LayOut from './LayOut';
import BackHeadWithUsername from '../middleComponents/BackHeadWithUsername';
import MakeSettingPanel from './MakeSettingPanel';

const data = user => ({
  title: '',
  list: [
    {
      key: 1,
      title: '',
      list: [
        {
          key: 1, type: 'link', to: '/twitter-clone/settings/screen_name', title: '', subTitle: user.name,
        },
        {
          key: 2, type: 'link', to: '/twitter-clone/settings/phone', title: '', subTitle: String(user.phone),
        },
        {
          key: 3, type: 'link', to: '/twitter-clone/settings/email', title: '', subTitle: user.email,
        },
        {
          key: 4, type: 'link', to: '/twitter-clone/settings/password', title: '',
        },
        {
          key: 5, type: 'link', to: '/twitter-clone/settings/security', title: '',
        },
      ],
    },
    {
      key: 2,
      title: '',
      list: [
        {
          key: 1, type: 'link', to: '/twitter-clone/settings/language', title: '', subTitle: user.language,
        },
        {
          key: 2, type: 'link', to: '/twitter-clone/settings/country', title: '/', subTitle: user.country,
        },
        {
          key: 3, type: 'link', to: '/twitter-clone/settings/your_data', title: '',
        },
        {
          key: 4, type: 'link', to: '/twitter-clone/settings/applications', title: '',
        },
      ],
    },
    {
      key: 3,
      list: [
        {
          key: 1, type: 'link', to: '/twitter-clone/settings/deactivate', title: '',
        },
      ],
    },
  ],
});

function Account({ user }) {
  return (
    <LayOut
      narrowHead={<BackHeadWithUsername title="" />}
      rightAside={(
        <MakeSettingPanel data={data(user)} />
      )}
    />
  );
}
Account.propTypes = {
  user: PropTypes.shape({
    name: PropTypes.string.isRequired,
    phone: PropTypes.number.isRequired,
    email: PropTypes.string.isRequired,
    language: PropTypes.string.isRequired,
    country: PropTypes.string.isRequired,
  }).isRequired,
};
const mapStateToProps = state => ({
  user: state.currentUser,
});
export default connect(mapStateToProps)(Account);
