import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { func, number } from 'prop-types';
import { whiteBackground } from '../themes';
import Avatar from '../BaseComponents/Avatar';
import CustomizedButton from '../BaseComponents/CustomizedButton';
import UserName from './UserName';
import Text from '../BaseComponents/Text';
import { hide, setHideTimerId as setHideTimer } from '../actionCreators/userInfoPopover';
import { userType, positionType } from '../propTypes';

const Container = styled.div`
  ${whiteBackground}
  border-radius: 13px; 
  padding: 13px;
  position: fixed;
  left: ${props => `${props.left}px`};
  top: ${props => `${props.top}px`};
  max-width: 300px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.22) 0px 6px 6px;
`;
const HeadWrapper = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
`;
const FollowingWrapper = styled.span`
  margin-right: 18px;
`;
const MarginTop = styled.div`
  margin-top: 9px;
`;
/**
 * +------------+-----------------------------------------------+
 * |            |                                follow button  |
 * |   avatar   |                                               |
 * |            |                                               |
 * +------------+-----------------------------------------------+
 * | name                                                       |
 * | screenName                                                 |
 * +------------------------------------------------------------+
 * |                                                            |
 * |                        description                         |
 * |                                                            |
 * +------------------------------------------------------------+
 * |  xxx    xx                                  |
 * +------------------------------------------------------------+
 * | / xxx                          |
 * +------------------------------------------------------------+
 * @param {} param0
 */
function UserInfoPopover({
  position, user, hideSelf, hideTimerId, setHideTimerId,
}) {
  const delay = 300;
  function handleMouseEnter() {
    if (hideTimerId) {
      clearTimeout(hideTimerId);
    }
  }
  function handleMouseLeave() {
    const timerId = setTimeout(() => {
      hideSelf();
    }, delay);
    setHideTimerId(timerId);
  }
  if (!position.left || !position.top) return null;
  return (
    <Container
      left={position.left}
      top={position.top}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <HeadWrapper>
        <Avatar user={user} />
        <CustomizedButton small></CustomizedButton>
      </HeadWrapper>
      <MarginTop>
        <UserName user={user} isTwoLine hoverable={false} />
      </MarginTop>
      <MarginTop>
        <Text>{user.desc}</Text>
      </MarginTop>
      <MarginTop>
        <FollowingWrapper>
          <Text bold>{user.following}</Text>
          <Text secondary></Text>
        </FollowingWrapper>
        <Text bold>{user.followers}</Text>
        <Text secondary></Text>
      </MarginTop>
      <MarginTop>
        <Text secondary></Text>
      </MarginTop>
    </Container>
  );
}
UserInfoPopover.propTypes = {
  position: positionType.isRequired,
  user: userType,
  hideSelf: func.isRequired,
  hideTimerId: number,
  setHideTimerId: func.isRequired,
};
UserInfoPopover.defaultProps = {
  user: null,
  hideTimerId: null,
};
const mapStateToProps = state => ({
  position: state.userInfoPopover.position,
  user: state.userInfoPopover.user,
  hideTimerId: state.userInfoPopover.hideTimerId,
});
const mapDispatchToProps = dispatch => ({
  hideSelf: () => dispatch(hide()),
  setHideTimerId: id => dispatch(setHideTimer(id)),
});
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(UserInfoPopover);
