import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import MakeSettingPanel from './MakeSettingPanel';

const data = user => ({
  title: '',
  list: [
    {
      key: 1,
      title: `@${user.name}`,
      list: [
        {
          key: 1, type: 'link', to: '/twitter-clone/settings/account', title: '', isNav: true,
        },
        {
          key: 2, type: 'link', to: '/twitter-clone/settings/safety', title: '', isNav: true,
        },
        {
          key: 3, type: 'link', to: '/twitter-clone/settings/notifications', title: '', isNav: true,
        },
        {
          key: 4, type: 'link', to: '/twitter-clone/settings/content_preferences', title: '', isNav: true,
        },
      ],
    },
    {
      key: 2,
      title: '',
      list: [
        {
          key: 1, type: 'link', to: '/twitter-clone/settings/data', title: '', isNav: true,
        },
        {
          key: 2, type: 'link', to: '/twitter-clone/settings/accessibility', title: '', isNav: true,
        },
        {
          key: 3, type: 'link', to: '/twitter-clone/settings/about', title: '', isNav: true,
        },
      ],
    },
  ],
});

function SettingPanel({ user }) {
  return (
    <MakeSettingPanel data={data(user)} />
  );
}
SettingPanel.propTypes = {
  user: PropTypes.shape({
    name: PropTypes.string.isRequired,
    phone: PropTypes.number.isRequired,
    email: PropTypes.string.isRequired,
    language: PropTypes.string.isRequired,
    country: PropTypes.string.isRequired,
  }).isRequired,
};
const mapStateToProps = state => ({
  user: state.currentUser,
});
export default connect(mapStateToProps)(SettingPanel);
